@import url(https://fonts.googleapis.com/css?family=Lato:300i,400,700);
body {
  margin: 0;
  padding: 0;
  background: #fff;
  font-family: 'Lato', sans-serif;
  font-size: calc(1em + 0.5vw);
}

@media (min-width: 1200px) {
  body {
    font-size: 1.6em;
  }
}

@media (max-width: 960px) {
  body {
    font-size: 1.2em;
  }
}

h1 {
  word-break: break-all;
}

.logo {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
}
.logo > .name {
  font-size: 6em;
  color: #363636;
}
@media (max-width: 960px) {
  .logo > .name {
    font-size: 12vw;
  }
}
.logo > .image {
  width: 20vw;
  max-width: 200px;
}
.no-bottom {
  padding-bottom: 0;
  margin-bottom: 0;
}
.no-top {
  padding-top: 0;
  margin-top: 0;
}
.big {
  font-size: 4em;
}

.Demo {
  padding-top: 80px;
  flex: 1 0 auto;
  display: flex;
  flex-flow: column nowrap;
  width: 100%;
}

button {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background-color: rgba(0, 0, 0, 0.4);
  border: none;
  border-radius: 4px;
  padding: 1em;
  margin: 0.5em 1em;
  cursor: pointer;
  color: #fff;
  font-size: calc(0.5em + 1vw);
}

@media (min-width: 1200px) {
  button {
    font-size: 1em;
  }
}

button:hover {
  background-color: rgba(0, 0, 0, 0.3);
}

.demo-add {
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  padding: 0 2em;
  outline: none;
}

.demo-stats {
  padding: 0 2em;
}

.Page {
  min-height: 100vh;
  box-sizing: border-box;
  padding: calc(60px + 2em) calc(50vw - 960px / 2 + 2em) 2em;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  background-position: center center;
  background-size: contain;
  background-repeat: no-repeat;
  overflow-x: hidden;
}

@media (max-width: 960px) {
  .Page {
    padding: 2em;
  }
}

.Page:first-child {
  margin-bottom: -60px;
  padding-bottom: calc(60px + 10vh);
}

.Page:nth-child(1) {
  background-color: #71c9ec;
  text-align: center;
}
.Page:nth-child(3) {
  background-color: #7aaa62;
}
.Page:nth-child(4) {
  background-color: #ffce64;
  text-align: center;
}
.Page:nth-child(5) {
  background-color: #ff62ff;
}
.Page:nth-child(6) {
  background-color: #62dded;
}
.Page:nth-child(7) {
  background-color: #6262ff;
}
.Page:nth-child(8) {
  background-color: #7acbc5;
}
.Page:nth-child(9) {
  background-color: #ffb862;
}
.Page:nth-child(10) {
  background-color: #fd6e63;
}

.Page[page^='edrone'] {
  padding-top: 0;
  background-color: #fd6e63;
  justify-content: center;
  position: relative;
  z-index: -1;
  background-image: url(/logo.svg);
  background-position: calc(50% + 240px) center;
  background-size: 360px;
  background-attachment: fixed;
  background-repeat: no-repeat;
}
.Page[page^='edrone']::before {
  content: '';
  position: absolute;
  z-index: -1;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background-image: -webkit-linear-gradient(
    bottom,
    #fd6e63,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    #fd6e63
  );
  background-image: linear-gradient(
    0deg,
    #fd6e63,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    transparent,
    #fd6e63
  );
}
.Page[page^='edrone'] > .FadeInBlock {
  flex: 0 0 auto;
}

.Page.tidslinje {
  background-color: #006ca4;
}

.Page.tidslinje-1 {
  background-color: #71c9ec;
  background-image: url(/timeline-person.png);
  background-position: center 60%;
  background-size: 180px;
}
.Page.tidslinje-2 {
  background-color: #fd6e63;
  background-image: url(/timeline-joystick.png), url(/timeline-drone.png);
  background-position: center 45%, center 90%;
  background-size: 130px, 180px;
  background-blend-mode: darken;
}
.Page.tidslinje-3 {
  background-color: #ffce64;
  background-image: url(https://news.filehippo.com/wp-content/uploads/2014/10/ambulance-drone.png);
  background-position: center center;
  background-size: cover;
  background-blend-mode: multiply;
  background-attachment: fixed;
}
.Page.Page.tidslinje-3 .FadeInBlock:nth-child(4) {
  background-image: url(/timeline-nesespray.png);
  background-position: 200px center;
  background-size: contain;
  background-repeat: no-repeat;
  padding-right: 160px;
}
.Page.tidslinje-4 {
  background-color: #7acbc5;
  background-image: url(/timeline-hospital.png), url(/timeline-house.png);
  background-position: calc(50% - 240px) 60%, calc(50% + 240px) 90%;
  background-size: 180px;
}

@media (max-width: 960px) {
  .Page.tidslinje-4 {
    background-position: 25% 50%, 75% 90%;
  }
  .Page {
    background-blend-mode: multiply;
  }
  .Page.tidslinje-3 {
    background-position: center 80%, center center;
  }
  .Page.Page.tidslinje-3 .FadeInBlock:nth-child(4) {
    background-image: none;
    padding-right: 0;
  }
  .Page[page^='edrone'] {
    background-position: 75% center;
  }
}

.Tabs {
  display: flex;
  flex-direction: row;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  bottom: 0;
  overflow-x: auto;
  z-index: 2;
}

.Tabs > a {
  display: flex;
  padding: 0 1em;
  flex: 1 0 auto;
  height: 60px;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.3);
  text-decoration: none;
  color: #fff;
}

.Tabs > .selected {
  background-color: rgba(0, 0, 0, 0.6);
}

.FadeInBlock {
  max-width: 320px;
  margin: 16px;
  flex: 1 0 auto;
  position: relative;
  opacity: var(--opacity);
  will-change: opacity, left, right;
}

.FadeInBlock:first-of-type {
  margin-top: 4em;
}

.FadeInBlock > h3,
.FadeInBlock > p {
  margin: 0;
  padding: 0;
}

.FadeInBlock[side='left'] {
  align-self: flex-start;
  right: var(--slideIn);
}

.FadeInBlock[mode='fill'] {
  height: 80vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.FadeInBlock[side='right'] {
  align-self: flex-end;
  left: var(--slideIn);
}

